import React, { useState, useEffect, useRef, useCallback } from 'react';

// --- MOCK DATA & SERVICES ---
// In your actual app, these would be in separate files.

// Define the TypeScript type for a Post
// Based on its usage in the component
type Post = {
  _id: string;
  author: {
    _id: string;
    username: string;
    profile: {
      avatar?: string;
    };
  };
  content: string;
  media: { url: string; type: 'image' | 'video' }[];
  likes: string[];
  comments: any[]; // Replace 'any' with a proper Comment type in your app
  stats: {
    likesCount: number;
    commentsCount: number;
  };
  createdAt: string;
};

// Mock User Data
const mockUser = {
  _id: 'user-123',
  username: 'You',
  profile: {
    avatar: 'https://placehold.co/100x100/7c3aed/ffffff?text=U',
  },
};

// Mock Auth Store Hook (e.g., from Zustand)
const useAuthStore = () => ({
  user: mockUser,
});

// A function to generate mock posts for the feed
const generateMockPosts = (page: number, limit: number): Post[] => {
  if (page > 5) return []; // Stop generating after 5 pages
  return Array.from({ length: limit }, (_, i) => {
    const postId = `post_${page}_${i}`;
    return {
      _id: postId,
      author: {
        _id: `user_${i}`,
        username: `user_${Math.floor(Math.random() * 1000)}`,
        profile: {
          avatar: `https://placehold.co/100x100/a855f7/ffffff?text=A${i}`,
        },
      },
      content: `This is post number ${i + (page - 1) * limit}. It's a great day for some mock data and infinite scrolling!`,
      media: i % 3 === 0 ? [{ url: `https://placehold.co/600x400/1e1b4b/ffffff?text=Image+${i}`, type: 'image' }] : [],
      likes: [],
      comments: [],
      stats: {
        likesCount: Math.floor(Math.random() * 100),
        commentsCount: Math.floor(Math.random() * 20),
      },
      createdAt: new Date().toISOString(),
    };
  });
};

// Mock Post Service
const postService = {
  getFeedPosts: async (page = 1, limit = 10) => {
    console.log(`Fetching page ${page}...`);
    // Simulate network delay
    await new Promise(resolve => setTimeout(resolve, 500));
    const items = generateMockPosts(page, limit);
    return {
      data: {
        items,
        pagination: { hasNextPage: items.length > 0 && page < 5 },
      },
    };
  },
  createPost: async (postData: { content: string }) => {
    console.log('Creating post:', postData);
    await new Promise(resolve => setTimeout(resolve, 500));
    const newPost: Post = {
      _id: `new_post_${Date.now()}`,
      author: mockUser,
      content: postData.content,
      media: [],
      likes: [],
      comments: [],
      stats: { likesCount: 0, commentsCount: 0 },
      createdAt: new Date().toISOString(),
    };
    return newPost;
  },
  toggleLikePost: async (postId: string) => {
    console.log(`Toggling like for post ${postId}`);
    await new Promise(resolve => setTimeout(resolve, 200));
    // In a real app, the backend would tell you the new state.
    // Here we just simulate it.
    const isLiked = Math.random() > 0.5;
    return {
      isLiked,
      likesCount: Math.floor(Math.random() * 101), // return a new random count
    };
  },
  addComment: async (postId: string, text: string) => {
    console.log(`Adding comment to ${postId}: ${text}`);
    await new Promise(resolve => setTimeout(resolve, 300));
    const newComment = {
      _id: `comment_${Date.now()}`,
      author: mockUser,
      text,
      createdAt: new Date().toISOString(),
    };
    return {
      comment: newComment,
      commentsCount: Math.floor(Math.random() * 21), // return a new random count
    };
  },
};

// Mock Story Service
const storyService = {
  createStory: async (storyData: any) => {
    console.log('Creating story:', storyData);
    await new Promise(resolve => setTimeout(resolve, 500));
    return { success: true };
  },
};


// --- UI HELPER COMPONENTS ---
// In your actual app, these would be in separate files.

// Icon Component (replaces Heroicons)
const PlusIcon = ({ className }: { className?: string }) => (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={className || "w-6 h-6"}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M12 4.5v15m7.5-7.5h-15" />
  </svg>
);

const HeartIcon = ({ className, solid = false }: { className?: string, solid?: boolean }) => (
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill={solid ? 'currentColor' : 'none'} strokeWidth={1.5} stroke="currentColor" className={className || "w-6 h-6"}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M21 8.25c0-2.485-2.099-4.5-4.688-4.5-1.935 0-3.597 1.126-4.312 2.733-.715-1.607-2.377-2.733-4.313-2.733C5.1 3.75 3 5.765 3 8.25c0 7.22 9 12 9 12s9-4.78 9-12z" />
  </svg>
);

const ChatBubbleIcon = ({ className }: { className?: string }) => (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={className || "w-6 h-6"}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M2.25 12.76c0 1.6 1.123 2.994 2.707 3.227 1.087.16 2.185.283 3.293.369V21l4.076-4.076a1.526 1.526 0 011.037-.443 48.282 48.282 0 005.68-.494c1.584-.233 2.707-1.626 2.707-3.228V6.741c0-1.602-1.123-2.995-2.707-3.228A48.394 48.394 0 0012 3c-2.392 0-4.744.175-7.043.513C3.373 3.746 2.25 5.14 2.25 6.741v6.018z" />
  </svg>
);


// Button Component
const Button = ({ children, onClick, variant = 'primary', size = 'md', icon }: { children: React.ReactNode, onClick: () => void, variant?: string, size?: string, icon?: React.ReactNode }) => {
  const baseClasses = "flex items-center justify-center font-semibold rounded-full transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-offset-gray-900";
  const variantClasses = variant === 'primary' ? 'bg-indigo-600 hover:bg-indigo-700 text-white focus:ring-indigo-500' : 'bg-gray-700 hover:bg-gray-600 text-gray-200 focus:ring-gray-500';
  const sizeClasses = size === 'sm' ? 'px-3 py-1.5 text-sm' : 'px-4 py-2';
  
  return (
    <button onClick={onClick} className={`${baseClasses} ${variantClasses} ${sizeClasses}`}>
      {icon && <span className="mr-2">{icon}</span>}
      {children}
    </button>
  );
};

// Modal Components
const CreatePostModal = ({ isOpen, onClose, onCreatePost }: { isOpen: boolean, onClose: () => void, onCreatePost: (data: any) => Promise<void> }) => {
  const [content, setContent] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  if (!isOpen) return null;

  const handleSubmit = async () => {
    if (!content.trim() || isSubmitting) return;
    setIsSubmitting(true);
    try {
      await onCreatePost({ content });
      setContent('');
      onClose();
    } catch (error) {
      console.error("Failed to create post from modal", error);
      // Here you could show an error message to the user
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50">
      <div className="bg-gray-800 border border-gray-700 rounded-2xl p-6 w-full max-w-md mx-4">
        <h2 className="text-xl font-bold text-white mb-4">Create Post</h2>
        <textarea
          value={content}
          onChange={(e) => setContent(e.target.value)}
          placeholder="What's on your mind?"
          className="w-full h-32 p-3 bg-gray-900 rounded-lg text-white border border-gray-700 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
        />
        <div className="flex justify-end space-x-3 mt-4">
          <Button onClick={onClose} variant="secondary">Cancel</Button>
          <Button onClick={handleSubmit}>// disabled={isSubmitting}
            {isSubmitting ? 'Posting...' : 'Post'}
          </Button>
        </div>
      </div>
    </div>
  );
};

const CreateStoryModal = ({ isOpen, onClose, onCreateStory }: { isOpen: boolean, onClose: () => void, onCreateStory: (data: any) => Promise<void> }) => {
  if (!isOpen) return null;
  // Simplified version
  return (
    <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50">
      <div className="bg-gray-800 border border-gray-700 rounded-2xl p-6 w-full max-w-md mx-4">
        <h2 className="text-xl font-bold text-white mb-4">Create Story</h2>
        <p className="text-gray-400 mb-4">Story creation UI would go here.</p>
        <div className="flex justify-end space-x-3 mt-4">
          <Button onClick={onClose} variant="secondary">Cancel</Button>
          <Button onClick={async () => { await onCreateStory({}); onClose(); }}>Create</Button>
        </div>
      </div>
    </div>
  );
};

// StoriesRing Component
const StoriesRing = ({ onCreateStory }: { onCreateStory: () => void }) => (
  <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-4 border border-gray-700 flex items-center space-x-4 overflow-x-auto">
    <button onClick={onCreateStory} className="flex-shrink-0 flex flex-col items-center space-y-1 text-center">
      <div className="w-16 h-16 rounded-full bg-gray-700 border-2 border-dashed border-gray-500 flex items-center justify-center text-gray-400 hover:bg-gray-600 transition-colors">
        <PlusIcon className="w-6 h-6" />
      </div>
      <span className="text-xs text-gray-300">Your Story</span>
    </button>
    {/* Mock stories */}
    {Array.from({ length: 8 }).map((_, i) => (
      <div key={i} className="flex-shrink-0 flex flex-col items-center space-y-1 text-center">
        <div className="w-16 h-16 rounded-full p-0.5 bg-gradient-to-tr from-yellow-400 via-red-500 to-purple-500">
          <div className="bg-gray-800 p-0.5 rounded-full">
            <img src={`https://placehold.co/100x100/1e1b4b/ffffff?text=S${i}`} alt={`Story ${i}`} className="w-full h-full object-cover rounded-full" />
          </div>
        </div>
        <span className="text-xs text-gray-400">user_{i}</span>
      </div>
    ))}
  </div>
);

// PostCard Component
const PostCard = ({ post, onLike, currentUserId }: { post: Post, onLike: (id: string) => void, onComment: (id: string, text: string) => void, currentUserId?: string }) => {
  const isLikedByCurrentUser = currentUserId ? post.likes.includes(currentUserId) : false;

  return (
    <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl border border-gray-700 overflow-hidden">
      <div className="p-4 flex items-center space-x-3">
        <img src={post.author.profile.avatar} alt={post.author.username} className="w-10 h-10 rounded-full object-cover" />
        <div>
          <p className="font-semibold text-white">{post.author.username}</p>
          <p className="text-xs text-gray-400">{new Date(post.createdAt).toLocaleString()}</p>
        </div>
      </div>
      <p className="px-4 pb-4 text-gray-300">{post.content}</p>
      {post.media.length > 0 && (
        <img src={post.media[0].url} alt="Post media" className="w-full h-auto" />
      )}
      <div className="p-4">
        <div className="flex items-center justify-between text-gray-400">
            <div className="flex items-center space-x-4">
                <button onClick={() => onLike(post._id)} className={`flex items-center space-x-1.5 group ${isLikedByCurrentUser ? 'text-red-500' : 'hover:text-red-500'}`}>
                    <HeartIcon className="w-6 h-6 transition-transform group-hover:scale-110" solid={isLikedByCurrentUser} />
                    <span>{post.stats.likesCount}</span>
                </button>
                <button className="flex items-center space-x-1.5 hover:text-indigo-400 group">
                    <ChatBubbleIcon className="w-6 h-6 transition-transform group-hover:scale-110" />
                    <span>{post.stats.commentsCount}</span>
                </button>
            </div>
        </div>
      </div>
    </div>
  );
};


// --- MAIN HOME COMPONENT ---

const Home: React.FC = () => {
  const { user } = useAuthStore();
  const [posts, setPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [page, setPage] = useState(1);
  const observerRef = useRef<HTMLDivElement>(null);

  // Modals state
  const [showCreatePost, setShowCreatePost] = useState(false);
  const [showCreateStory, setShowCreateStory] = useState(false);

  // Load initial data
  useEffect(() => {
    const loadInitialData = async () => {
      try {
        setLoading(true);
        const postsResponse = await postService.getFeedPosts(1, 10);
        
        setPosts(postsResponse.data.items);
        setHasMore(postsResponse.data.pagination.hasNextPage);
        setPage(2);
      } catch (error) {
        console.error('Error loading feed:', error);
      } finally {
        setLoading(false);
      }
    };
    loadInitialData();
  }, []);

  const loadMorePosts = useCallback(async () => {
    if (loadingMore || !hasMore) return;
    try {
      setLoadingMore(true);
      const response = await postService.getFeedPosts(page, 10);
      setPosts(prev => [...prev, ...response.data.items]);
      setHasMore(response.data.pagination.hasNextPage);
      setPage(prev => prev + 1);
    } catch (error) {
      console.error('Error loading more posts:', error);
    } finally {
      setLoadingMore(false);
    }
  }, [page, hasMore, loadingMore]);


  // Infinite scroll observer
  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          loadMorePosts();
        }
      },
      { threshold: 0.5 } // Trigger when 50% of the element is visible
    );

    const currentObserverRef = observerRef.current;
    if (currentObserverRef) {
      observer.observe(currentObserverRef);
    }

    return () => {
      if (currentObserverRef) {
        observer.unobserve(currentObserverRef);
      }
    };
  }, [loadMorePosts]);

  const handleCreatePost = async (postData: any) => {
    try {
      const newPost = await postService.createPost(postData);
      setPosts(prev => [newPost, ...prev]);
    } catch (error) {
      console.error('Error creating post:', error);
      throw error;
    }
  };

  const handleCreateStory = async (storyData: any) => {
    try {
      await storyService.createStory(storyData);
      // In a real app, you might want to refetch stories or update the UI
    } catch (error) {
      console.error('Error creating story:', error);
      throw error;
    }
  };

  const handleLikePost = async (postId: string) => {
    if (!user) return;
    // Optimistic UI update
    const originalPosts = [...posts];
    const postIndex = posts.findIndex(p => p._id === postId);
    if (postIndex === -1) return;

    const isCurrentlyLiked = posts[postIndex].likes.includes(user._id);
    const newLikesCount = isCurrentlyLiked ? posts[postIndex].stats.likesCount - 1 : posts[postIndex].stats.likesCount + 1;

    const updatedPosts = posts.map(post =>
      post._id === postId
        ? {
            ...post,
            likes: isCurrentlyLiked
              ? post.likes.filter(id => id !== user._id)
              : [...post.likes, user._id],
            stats: { ...post.stats, likesCount: newLikesCount }
          }
        : post
    );
    setPosts(updatedPosts);

    // Call API and handle potential failure
    try {
      const { isLiked, likesCount } = await postService.toggleLikePost(postId);
      // Update with server state to ensure consistency
      setPosts(prev => prev.map(post => 
        post._id === postId 
          ? { 
              ...post, 
              likes: isLiked 
                ? [...post.likes.filter(id => id !== user._id), user._id] // Ensure user is in list
                : post.likes.filter(id => id !== user._id),
              stats: { ...post.stats, likesCount }
            }
          : post
      ));
    } catch (error) {
      console.error('Error toggling like:', error);
      // Revert on failure
      setPosts(originalPosts);
    }
  };

  const handleAddComment = async (postId: string, text: string) => {
    try {
      const { comment, commentsCount } = await postService.addComment(postId, text);
      setPosts(prev => prev.map(post => 
        post._id === postId 
          ? { 
              ...post, 
              comments: [...post.comments, comment],
              stats: { ...post.stats, commentsCount }
            }
          : post
      ));
    } catch (error) {
      console.error('Error adding comment:', error);
      throw error;
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[50vh]">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-indigo-500"></div>
      </div>
    );
  }

  return (
    <div className="max-w-2xl mx-auto space-y-6 pb-20 px-2 sm:px-0">
      {/* Stories Section */}
      <StoriesRing onCreateStory={() => setShowCreateStory(true)} />

      {/* Create Post Section */}
      {user && (
        <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-4 border border-gray-700">
          <div className="flex items-center space-x-3">
            <div className="w-10 h-10 rounded-full overflow-hidden bg-gradient-to-tr from-purple-600 to-indigo-600">
              {user.profile.avatar ? (
                <img
                  src={user.profile.avatar}
                  alt="Your avatar"
                  className="w-full h-full object-cover"
                />
              ) : (
                <div className="w-full h-full flex items-center justify-center text-white font-medium">
                  {user.username[0].toUpperCase()}
                </div>
              )}
            </div>
            
            <button
              onClick={() => setShowCreatePost(true)}
              className="flex-1 text-left px-4 py-3 bg-gray-900/50 hover:bg-gray-900/70 rounded-full text-gray-400 transition-colors"
            >
              What's on your mind?
            </button>
            
            <Button
              onClick={() => setShowCreatePost(true)}
              variant="primary"
              size="sm"
              icon={<PlusIcon className="w-4 h-4" />}
            >
              Post
            </Button>
          </div>
        </div>
      )}

      {/* Posts Feed */}
      <div className="space-y-6">
        {posts.map((post) => (
          <PostCard
            key={post._id}
            post={post}
            onLike={handleLikePost}
            onComment={handleAddComment}
            currentUserId={user?._id}
          />
        ))}
      </div>

      {/* Loading more indicator */}
      {loadingMore && (
        <div className="flex justify-center py-8">
          <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-indigo-500"></div>
        </div>
      )}

      {/* Infinite scroll trigger */}
      {hasMore && <div ref={observerRef} className="h-4" />}

      {/* No more posts */}
      {!hasMore && posts.length > 0 && (
        <div className="text-center py-8">
          <p className="text-gray-400">You're all caught up!</p>
        </div>
      )}

      {/* Empty state */}
      {posts.length === 0 && !loading && (
        <div className="text-center py-12">
          <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-8 border border-gray-700">
            <h3 className="text-xl font-semibold text-white mb-2">Welcome to the Feed!</h3>
            <p className="text-gray-400 mb-6">It's looking a little empty. Start by creating a post.</p>
            <Button onClick={() => setShowCreatePost(true)} icon={<PlusIcon className="w-4 h-4" />}>
              Create Your First Post
            </Button>
          </div>
        </div>
      )}

      {/* Modals */}
      <CreatePostModal
        isOpen={showCreatePost}
        onClose={() => setShowCreatePost(false)}
        onCreatePost={handleCreatePost}
      />

      <CreateStoryModal
        isOpen={showCreateStory}
        onClose={() => setShowCreateStory(false)}
        onCreateStory={handleCreateStory}
      />

    </div>
  );
}

// The main App component to render our Home component
export default function App() {
  return (
    <div className="bg-gray-900 text-white min-h-screen font-sans py-8">
        <Home />
    </div>
  )
}
